@charset "UTF-8";
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

.body {
  height: 100%;
  font-family: "PT Sans", sans-serif;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  background: #e5e5e5;
}

.advantages {
  text-align: center;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: advantages__subtitle;
}
@media screen and (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__item {
  background-color: #eeeeee;
}

.advantages__item--img {
  display: grid;
}
@media screen and (min-width: 768px) {
  .advantages__item--img {
    grid-template-columns: repeat(3, 1fr);
    grid-column: span 3;
  }
}

.advantages__item--img::after {
  display: block;
  content: "";
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  grid-column: span 2;
}

.advantages__item--img:nth-child(1)::after {
  height: 260px;
  background-image: url("../img/photo-city-mobile.jpg");
}
@media screen and (min-width: 768px) {
  .advantages__item--img:nth-child(1)::after {
    background-image: url("../img/photo-city-tablet.jpg");
    order: 1;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__item--img:nth-child(1)::after {
    background-image: url("../img/photo-city-desctop.jpg");
  }
}

.advantages__item--img:nth-child(2)::after {
  height: 260px;
  background-image: url("../img/photo-brige-mobile.jpg");
}
@media screen and (min-width: 768px) {
  .advantages__item--img:nth-child(2)::after {
    background-image: url("../img/photo-brige-tablet.jpg");
    order: -1;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__item--img:nth-child(2)::after {
    background-image: url("../img/photo-brige-desctop.jpg");
  }
}

.advantages__item--border {
  position: relative;
}

.advantages__item--border::before {
  position: absolute;
  content: "";
  height: 1px;
  background-color: #d6d6d6;
  top: 0;
  left: 20px;
  right: 20px;
}

.advantages__item--padding {
  padding: 26px 18px;
}
@media screen and (min-width: 768px) {
  .advantages__item--padding {
    padding: 42px 34px 42px 33px;
  }
}

.advantages__item--line {
  background: url("../img/mobile-welcome-triangle-bottom.svg");
  background-repeat: no-repeat;
}
@media screen and (min-width: 768px) {
  .advantages__item--line {
    background: none;
  }
}

.advantages__item--background {
  background-color: #81b3d2;
  color: #ffffff;
}

.advantages__title {
  margin: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}
@media screen and (min-width: 768px) {
  .advantages__title {
    padding: 0 15px;
    font-size: 21px;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__title {
    padding: 0 100px;
  }
}

.advantages__subtitle {
  margin: 0;
  padding: 15px 0 0 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
}

.advantages__title--padding {
  padding: 39px 16px 0 16px;
}
@media screen and (min-width: 768px) {
  .advantages__title--padding {
    padding: 41px 52px 14px 52px;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__title--padding {
    padding: 47px 120px 14px 120px;
  }
}

.advantages__subtitle--padding {
  padding: 15px 16px 35px 16px;
}
@media screen and (min-width: 768px) {
  .advantages__subtitle--padding {
    padding: 0 32px 42px 32px;
  }
}

.advantages__subtitle::before {
  display: block;
  counter-increment: advantages__subtitle;
  content: "— №" counter(advantages__subtitle) " —";
  padding-bottom: 17px;
}

