.main-nav {
  display: flex;
  background-color: #ffffff;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  height: 56px;
}

.main-nav__logo {
  position: absolute;
  width: 185px;
  height: 96px;
  left: calc(50% - 90px);
  margin-bottom: -60px;
}

.main-nav__toggle {
  position: absolute;
  width: 56px;
  height: 56px;
  left: calc(90% - 20px);
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 3;
}
@media screen and (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--close .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: calc(50% - 2px);
  right: calc(50% - 12px);
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 -9px 0 0 #81b3d2, 0 9px 0 0 #81b3d2;
}

.main-nav--close .main-nav__item {
  display: none;
}
@media screen and (min-width: 768px) {
  .main-nav--close .main-nav__item {
    display: block;
  }
}

.main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: calc(50% - 2px);
  left: calc(50% - 14px);
  width: 28px;
  height: 4px;
  background-color: #ffffff;
  transform: rotate(45deg);
  z-index: 30;
}

.main-nav--open .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--open .main-nav__logo {
  display: none;
}

.main-nav--open .main-nav {
  padding-bottom: 0;
}

.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .main-nav__list {
    display: flex;
