    padding-left: 44px;
  }
}
@media screen and (min-width: 1200px) {
  .form__item--position-contacts .form__container {
    padding-left: 64px;
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-login .form__container {
    padding-right: 44px;
  }
}
@media screen and (min-width: 1200px) {
  .form__item--position-login .form__container {
    padding-right: 64px;
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-login::after {
    content: "";
    right: 0;
    bottom: -30px;
    top: -30px;
    width: 1px;
    background-color: #dbdbdb;
    position: absolute;
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-impression {
    grid-column: span 2;
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-impression .form__list {
    display: grid;
    grid-template-columns: repeat(2, 280px);
    justify-content: space-between;
    gap: 16px;
  }
}
@media screen and (min-width: 1200px) {
  .form__item--position-impression .form__list {
    padding-top: 28px;
    grid-template-columns: repeat(3, 280px);
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-attractions {
    grid-column: span 2;
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-attractions .form__list {
    display: grid;
    grid-template-columns: repeat(2, 200px);
    justify-content: center;
    column-gap: 60px;
  }
}
@media screen and (min-width: 1200px) {
  .form__item--position-attractions .form__list {
    padding-top: 42px;
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (min-width: 768px) {
  .form__item--position-emotions {
    grid-column: span 2;
  }
}

.form__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  padding: 0 5px 4px 5px;
}

@media screen and (min-width: 768px) {
  .form__title--border {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media screen and (min-width: 768px) {
  .form__title--border::before,
.form__title--border::after {
    content: "";
    width: 122px;
    height: 1px;
    background-color: #dbdbdb;
  }
}
@media screen and (min-width: 1200px) {
  .form__title--border::before,
.form__title--border::after {
    width: 280px;
  }
}

.form__container {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.form__label {
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  padding-top: 16px;
  padding-left: 16px;
}

.form__input {
  font-family: "PT Sans", sans-serif;
  border: none;
  min-height: 58px;
  background-color: #eeeeee;
  padding-left: 16px;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.form__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.form__textarea {
  font-family: "PT Sans", sans-serif;
  width: 100%;
  background-color: #eeeeee;
  border: none;
  margin-top: 20px;
  padding: 14px 21px 15px 21px;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}

.form__button {
  width: 281px;
}

.form__comment {
  margin: 0;
  text-align: right;
  font-size: 12px;
  line-height: 21px;
}
@media screen and (min-width: 768px) {
  .form__comment {
    margin-top: 19px;
  }
}

.checkbox {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  padding-top: 17px;
  padding-bottom: 17px;
  align-items: center;
}

.checkbox__input {
  width: 46px;
  height: 46px;
  background-color: #eeeeee;
}

.checkbox__subtitle {
  text-transform: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  padding-left: 16px;
}

.radio {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  padding-top: 17px;
  padding-bottom: 17px;
  align-items: center;
}

.radio__input {
  width: 46px;
  height: 46px;
  background-color: #eeeeee;
}

.radio__subtitle {
  text-transform: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  padding-left: 16px;
}